.header{
    background-color: $color-green-dark;
    padding: 1rem 0;
    &__container{
        display: flex;
        justify-content: space-between;
    }
    &__logo{
        width: 12rem;
    }
    .nav{
        display: flex;
        align-items: center;
        gap: 3rem;
        font-size: 1.4rem;
        color: white;
        span{
            font-weight: bold;
            font-size: 150%;
            position: relative;
            &::before{
                content: "";
                width: 100%;
                height: 3px;
                background-color: $color-green-light;
                position: absolute;
                bottom: -10%;
                left: 0;
                border-radius: 2rem;
            }
        }
    }
}