.registration{
    &__form{
        background-color:$color-green-dark;
        border-radius: 1rem;
        padding: 5rem 3rem;
        display: inline-flex;
        flex-direction: column;
        gap: 3rem;
        width: 40rem;
        box-shadow: 1rem 1rem 1rem rgba($color: #000000, $alpha: 0.2);
        input{
            padding: 2rem;
            border-radius: 1rem;
            border: none;
        }
        .message{
            padding-bottom: 10rem;
        }
    }
    &__content{
        display: grid;
        grid-template-columns: 1fr 70rem;
        align-items: center;
        .map{
            border-radius: 1rem;
            box-shadow: 1rem 1rem 1rem rgba($color: #000000, $alpha: 0.2);
        }
    }
}